<template>
	<view>
		<web-view :src="webUrl[page]" allow="*"></web-view>
	</view>
</template>

<script>
export default {
	data() {
		return {
			page: '',
			webUrl: {
				about: this.$config.url + '/base/article/content?id=11',
				contactUs: this.$config.url + '/base/article/content?id=12',
				help: this.$config.url + '/base/article/content?id=18',
				agreement: this.$config.url + '/base/article/content?id=16',
				privacy: this.$config.url + '/base/article/content?id=17'
			},
			title: ''
		};
	},
	onLoad(options) {
		this.page = options.type
		this.title = options.title
		uni.setNavigationBarTitle({
			title: this.$t(this.title)
		})
	},
}
</script>

<style lang="scss" scoped></style>
